    -webkit-box-shadow: rgb(0 0 0 / 20%) 0px 1px 5px;
            box-shadow: rgb(0 0 0 / 20%) 0px 1px 5px;
    cursor: pointer;
}

.hooticon:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}


